import { Component, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatTableDataSource } from '@angular/material/table';
import { FormListComponent } from '@fragments';
import { TypeOfReason } from '@models';
import { ApiService, SweetAlertService } from '@services';
import { COMMON_FUNCTION, ENDPOINTS, STATUSES } from '@shared';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-type-of-reason',
  templateUrl: './type-of-reason.component.html',
  styleUrls: ['./type-of-reason.component.scss']
})
export class TypeOfReasonComponent implements OnInit {

  @ViewChild(FormListComponent) formListComponent: FormListComponent;
  dataSource: MatTableDataSource<TypeOfReason>;
  displayedColumns: string[] = ['reason', 'status'];
  statusList = [...STATUSES];

  typeOfReason = new TypeOfReason();
  typeOfReasonList = [];

  typeOfReasonForm: FormGroup;

  constructor(
    private apiService: ApiService,
    private formBuilder: FormBuilder,
    private sweetAlertService: SweetAlertService
  ) { }

  ngOnInit(): void {
    this.getList();
    this.setFormValidator();
  }

  getList(): void {
    this.apiService.findAll(ENDPOINTS.typeOfReason).subscribe((res: any) => {
      this.typeOfReasonList = res.responseData.data;
      this.dataSource = new MatTableDataSource<TypeOfReason>(res.responseData.data);
      this.dataSource.paginator = this.formListComponent.paginator;
      this.dataSource.sort = this.formListComponent.sort;
    });
  }

  save(): void {
    this.apiService.save(ENDPOINTS.typeOfReason, this.typeOfReason).subscribe(
      (res: any) => {
        if (res !== undefined) {
          this.sweetAlertService.success(res);
        }
      }, (err: any) => {
        this.sweetAlertService.error(err);
      }, () => {
        // actions after subscription
        this.resetFormValidator();
        this.getList();
      });
  }

  getTypeOfReason(): void {
    this.apiService.findById(ENDPOINTS.typeOfReason, this.formListComponent.idForUpdate).subscribe((res: any) => {
      if (res) {
        this.typeOfReason = res.responseData.data;
      }
    });
  }

  update(): void {
    this.apiService.update(ENDPOINTS.typeOfReason, this.typeOfReason).subscribe(
      (res: any) => {
        if (res !== undefined) {
          this.sweetAlertService.success(res);
        }
      }, (err: any) => {
        this.sweetAlertService.error(err);
      }, () => {
        // actions after subscription
        this.resetFormValidator();
        this.getList();
      });
  }

  setFormValidator(): void {
    this.typeOfReason.status = COMMON_FUNCTION.ACTIVE;
    this.typeOfReasonForm = this.formBuilder.group({
      reason: ['', Validators.required],
      status: [''],
    });
  }

  resetFormValidator(): void {
    this.typeOfReason = new TypeOfReason();
    this.typeOfReason.status = COMMON_FUNCTION.ACTIVE;
    this.typeOfReasonForm.markAsUntouched();
    this.typeOfReasonForm.markAsPristine();
  }

  delete() {
    Swal.fire({
      text: 'Are you sure you want to delete this type of reason?',
      icon: 'question',
      showCancelButton: true,
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      confirmButtonText: 'Confirm'
    }).then((result) => {
      if (result.value) {
        this.apiService.deleteById(ENDPOINTS.typeOfReason, this.typeOfReason.id).subscribe(
          () => {
            this.sweetAlertService.customSuccessMessage('Type of reason successfully deleted.');
            this.resetFormValidator();
            this.getList();
          }
        );
      }
    });
  }

}
